import React from 'react';
import '../styles/program.scss';
import FranceGallery from './SpainGallery';

const France = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Francja</h1>
                    <h2>Francja</h2>
                    <h4>Kady czowiek ma dwie ojczyzny: swoj wasn oraz Francj.e de Vegi.
                       <span>Henri de Bornier</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <FranceGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Nocny wyjazd w kierunku Francji. Przyjazd na nocleg w okolice Strasburga.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Przejazd na zwiedzanie Strasburga, ktrego zabytkowe centrum Strasburga zostao w caoci wpisane na List wiatowego Dziedzictwa UNESCO. Strasburg to rwnie miasto w ktrym przeszo spotyka si z nowoczesnoci, polityka z histori a alzacka mentalno z europejskim kosmopolityzmem. Spacer po Grand Ile,ktrej gwnym zabytkiem jest katedra Notre-Dame, nastpnie do urokliwej  Petite France- dzielnicy Strasburga, w ktrej znajduj si hotele i knajpki, w ktrych skosztowa mona typowo alzackiego flammekeche. Po zwiedzaniu przejazd w okolice Awinionu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Awinionu. Miasto papiey, pooone na brzegu Rodanu, pera  Prowansji, gdzie na kadym kroku czu zapach lawendy i zi. Spacer po miasteczku, podczas ktrego zobaczymy: synny most awinioski  Most w. Benezeta, Paac Papieski, katedr Notre Dame de Domes, a cae redniowieczne miasteczko zamknite jest w wietnie zachowanych murach obronnych- les Remparts. Nastpnie przejazd do hotelu na wybrzeu Costa Brava. Zakwaterowanie. Odpoczynek. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do stolicy Katalonii  Barcelony. Zobaczymy najwiksze dzieo Gaudiego: koci Sagrada Familia. Przejazd na wzgrze Montjuic, Plac Hiszpaski, Plac Kataloski. Spacer po Las Ramblas  gwny deptak ze synn hal targow La Boqueria, Dzielnica Gotycka, katedra w. Eulalii. Czas wolny. Po poudniu przejazd do hotelu. Zakwaterowanie, kolacja, nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p> Przejazd do stolicy paelli- Walencji. Spacer po najstarszej dzielnicy miasta: katedra, Plaza de la Reina, Mercado Central budynek Giedy Jedwabiu, wpisany na list UNESCO. Wizyta w Ciudad de las Artesy las Ciencias  Miasteczku Sztuki i Nauki. Kompleks futurystycznych obiektw autorstwa Santiago Calatravy jest symbolem Walencji. Po zwiedzaniu wyjazd do hotelu w okolicach Granady. Zakwaterowanie, obiadokolacja, nocleg.</p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Granady. Zwiedzanie przepiknej Alhambry- najpikniejszego paacu Orientu i symbolu muzumaskiego raju. Zesp zamkowo-paacowy wpisany jest na list wiatowego dziedzictwa UNESCO. Nastpnie przejazd do Kordoby, miasta o orientalnym charakterze, z historycznym centrum wpisanym na list UNESCO. Zwiedzanie meczetu Mezquita, jednego z najwspanialszych na wiecie przykadw arabskiej architektury. Przejazd do hotelu. Zakwaterowanie, kolacja, nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Sewilli, stolicy Andaluzji. Zwiedzanie: stara dzielnica ydowska Santa Cruz, pena malowniczych zaukw, Katedra Najwitszej Marii Panny  bdca jedn z najwikszych budowali sakralnych wiata i jedn z najpikniejszych wity w stylu gotyckim. Zobaczymy rwnie Alkazar- paac krlewski w stylu mudjar, Plac Hiszpaski i Plac Ameryki. Przejazd do hotelu. Zakwaterowanie, kolacja, nocleg.</p>
                        </div>

                        <div className="eighth-day">
                            <h5>Dzie VIII</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Madrytu- stolicy i najwikszego miasta Hiszpanii, Panoramiczne zwiedzanie: Plaza Mayor, Plac Hiszpaski z pomnikiem Cervantesa, paac krlewski, plac Orientu. Przejazd do hotelu. Zakwaterowanie, kolacja, nocleg.</p>
                        </div>

                        <div className="ninth-day">
                            <h5>Dzie IX</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Burgos. Zwiedzanie okazaej, gotyckiej, katedry, nazywanej dzieem aniow. Ze uwagi na wspania architektur oraz zebrane dziea sztuki zostaa wpisana na list UNESCO. Nastpnie przejazd do San Sebastian baskijskiego miasteczka pooonego nad Morzem Kantabryjskim. Czas wolny na odpoczynek nad zatok La Concha (czyli muszla) oraz degustacj lokalnych tapas, ktre uchodz za najwspanialsze w Hiszpanii. Przejazd do hotelu. Zakwaterowanie. Obiadokolacja, nocleg.</p>
                        </div>

                        <div className="tenth-day">
                            <h5>Dzie X</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Bordeaux. To miasto portowe w poudniowo-zachodniej Francji, pooone w zakolu rzeki Garonny, jest stolic francuskiej Akwitanii. Tu powstaa najszersza w Europie esplanada (Esplanada des Quinconces) - szeroka ulica z alej spacerow oraz tarasy Ludwika XVIII z ktrych rozpociera si przepikna panorama rzeki. Zobaczymy rwnie przepikny gmach Teatru Wielkiego (Grand Thtre), barokowy koci Marii Panny, romasko-gotyck katedr w. Andrzeja oraz pozostaoci dawnych murw obronnych. W 2007 roku zabytkowe centrum Bordeaux zostao wpisane na List wiatowego Dziedzictwa UNESCO. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="eleventh-day">
                            <h5>Dzie XI</h5>
                            <p>niadanie. Wykwaterowanie. Cigy przejazd do Polski.</p>
                        </div>
                        <div className="twelfth-day">
                            <h5>Dzie XII</h5>
                            <p>Przyjazd do Polski. Godziny uzalenione od miejsca wyjazdu.</p>
                        </div>

                    </div>
                </div>
            </section>

        </>
    );
}

export default France;