import React, { Component } from 'react';
import '../styles/AboutUs.scss';
import aboutPhoto from '../img/about.jpg';
import Pulse from 'react-reveal/Pulse';
import Fade from 'react-reveal/Fade';
import Zoom from 'react-reveal/Zoom';



const AboutUs = () => {
    return (
        <>
            <section className="aboutUs" id="aboutUs">
                <Pulse><h1>Kilka sw o nas</h1></Pulse>
                <div>
                    <Fade left cascade> <p>Opowiem Pastwu o podrach. O tych, ktre odbyam, ktre planuj, o ktrych marz. Podr to cz mojego ycia i moja pasja. Opowiem o miejscach, w ktre chciaabym Was zabra i podzieli si tym wszystkim, czego sama dowiadczyam. Pragn pokaza Wam najpikniejsze miejsca Europy, wsplnie wydoby ich esencj, obejrze najwaniejsze zabytki, nawiedzi wzniose sanktuaria, zaproponowa najciekawsze atrakcje, ale rwnie zej z utartych szlakw, zapuci si w tajemne zauki i wtopi w klimat odwiedzanych miejsc.  Chc Wam opowiedzie o historii, ludziach, zwyczajach, kulturze, sztuce, ciekawostkach i o tym wszystkim, czego zechcecie sucha.  W wolnej chwili posuchamy piosenki, obejrzymy film  to te jest opowie.  Nasz podr urozmaicimy dobrym lunchem z lampk wina, wymienit kaw i lodami. Usidziemy w kawiarni, gdzie rozkoszujc si lokalnymi specjaami, oddamy si wnikliwej obserwacji i poczujemy jak nas pochania atmosfera tego miejsca.
                    Gdziekolwiek wanie bdziemy.
                    W niniejszym folderze przedstawiam kilka przykadowych propozycji wycieczek i pielgrzymek, na ktre chciaabym Pastwa zaprosi. Niniejszy folder, to krtka opowie o tym, co warto zobaczy.
                    <span>Zapraszam serdecznie</span>
                        <span>Anna Gajewska</span>
                    </p></Fade>
                    <Zoom> <img src={aboutPhoto} alt="" /></Zoom>
                </div>
            </section>
        </>
    );


}


export default AboutUs;