import React from 'react';
import '../styles/Tours.scss';
import Fade from 'react-reveal/Fade';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import spain from '../img/spain.jpg';
import barcelona from '../img/barcelona.jpg';
import azulejos from '../img/azulejos.jpg';
import france from '../img/france-eiffla.jpg';
import italy from '../img/rome.jpg';
import croatia from '../img/croatia-dubrovnik.jpg';

import Spain from './Spain';
import SpainBarca from './SpainBarca';
import Portugal from './Portugal';
import France from './France';
import Croatia from './Croatia';



const Tours = () => {
    return (
        <Router>
            <Route path="/spain" component={Spain} />
            <Route path="/spain-barcelona" component={SpainBarca} />
            <Route path="/portugal" component={Portugal} />
            <Route path="/france" component={France} />
            <Route path="/croatia" component={Croatia} />


            <section className="tours" id="tours">
                <Fade left cascade><h1>Wycieczki</h1></Fade>
                <div>
                    <Fade top><div className="spain">
                        <div className="tour-photo">
                            <img src={spain} alt="" />
                        </div>
                        <h2>Hiszpania</h2>
                        <h3>Siesta, fiesta i maniana</h3>
                        <p> I ta muzyka czarnosrebrna, srebrnozota nagle wybucha i wypenia noc po brzegi, I bez flamenco nie zrozumiesz Don Kichota, Garci De Lorci i Lope de Vegi. <span>Wojciech Mynarski</span></p>
                        <button>
                            <Link to="/spain">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade bottom><div className="spain-barcelona">
                        <div className="tour-photo">
                            <img src={barcelona} alt="" />
                        </div>
                        <h2>Hiszpania</h2>
                        <h3>Costa Brava i Rumba de Barcelona</h3>
                        <p>Jedno serce, jedna ona, kataloska Barcelona. Jedno serce, jedno bicie, Barcelona ponad ycie <span>przypiewka kibicw FC Barcelony</span>  </p>
                        <button><Link to="/spain-barcelona" >czytaj wicej</Link></button>
                    </div></Fade>

                    <Fade top><div className="portugal">
                        <div className="tour-photo">
                            <img src={azulejos} alt="" />
                        </div>
                        <h2>Portugalia</h2>
                        <h3>Azulejos sardynki i wielkie odkrycia</h3>
                        <p>Nie znam kwiatw o podobnej rnorodnoci kolorw jak Lizbona w socu <span>Fernando Pesoa</span> </p>
                        <button><Link to="/portugal" >czytaj wicej</Link></button>
                    </div></Fade>

                    <Fade><div className="france">
                        <div className="tour-photo">
                            <img src={france} alt="" />
                        </div>
                        <h2>Francja</h2>
                        <h3>Francja</h3>
                        <p>Kady czowiek ma dwie ojczyzny: swoj wasn oraz Francj. <span>Henri de Bornier </span></p>
                        <button>
                            <Link to="/france">czytaj wicej</Link>
                        </button>

                    </div></Fade>

                    <Fade><div className="italy">
                        <div className="tour-photo">
                            <img src={italy} alt="" />
                        </div>
                        <h2>Wochy</h2>
                        <h3>Wochy</h3>
                        <p>Wochy to kraj peen licznych pokus pocigajcych do zboczenia z drogi prostej i uczciwej.<span>Zygmunt III Waza</span></p>

                        <button>
                            <Link to="/spain">czytaj wicej</Link>
                        </button>

                        {/* <Route path="/spain" component={Spain} /> */}
                    </div></Fade>

                    <Fade><div className="croatia">
                        <div className="tour-photo">
                            <img src={croatia} alt="" />
                        </div>
                        <h2>Chorwacja</h2>
                        <h3>Chorwacja</h3>
                        <p>Zadar ma najpikniejsze zachody soca na wiecie. Pikniejsze od tych oklaskiwanych kadego wieczora w Key West na Florydzie<span>Alfred Hitchcock</span></p>
                        <button>
                            <Link to="/croatia">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                </div>


            </section>
        </Router>
    );
}

export default Tours;