import React from 'react';
import '../../styles/program.scss';
// import ViennaGallery from './ViennaGallery';

const Vienna = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Muzyczny Wiede</h1>
                    <h4>BRAK OPISU!!!!!!!!!!!!!!<span>!!!!!!!!!!!!!</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    {/* <ViennaGallery /> */}
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka. Wyjazd w kierunku Wiednia o godzinie. Przyjazd do hotelu w okolicach Wiednia. Zakwaterowanie. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>Rano po niadaniu wyruszymy na zwiedzanie stolicy Austrii. Dzie rozpoczniemy od fundamentw, ktre pozwol nam pniej na zrozumienie tego piknego miasta- czyli przejazdem reprezentacyjn alej miasta Ringstrasse, przy ktrej ulokowane s najpikniejsze budowle w miecie: Urania, Muzeum Sztuki Uytkowej, Park Miejski, Opera, plac Marii Teresy, Hofburg, Parlament, Ratusz, Teatr Zamkowy, Uniwersytet, Koci Wotywny, budynek Giedy, Pawilony Otto Wagnera, najwikszy koci barokowy Karola Boromeusza, Plac Karola z Towarzystwem Muzycznym, gdzie odbywa si Koncert Noworoczny itd. Przejedziemy rwnie w stron synnego Prateru i zobaczymy najstarsze w Europie wesoe miasteczko wraz z Diabelskim Mynem. Nastpnie udamy si na Stare Miasto i zobaczymy jego gwn cz. Geograficzny i duchowny rodek Wiednia wyznacza strzelista wiea katedry w. Szczepana oraz Plac w. Szczepana. witynia jest arcydzieem gotyku, uchodzi za jedn z najpikniejszych europejskich katedr. Od ponad 800 lat bije w niej religijne serce miasta. Otoczenie, w ktrym si znajduje jest kwintesencj ducha Wiednia, gdzie nowoczesno przeplata si z histori. W niedalekiej odlegoci znajduje si synny Kolhmarkt  targ wglowy, bdcy siedzib najbardziej ostentacyjnych, luksusowych domw mody. Wizyta w Hofburgu, ktrego zabudowania obejmuj dawn miejsk rezydencj Habsburgw, koci, kaplic, Hiszpask Szko Jazdy (z zewntrz), muzea i Austriack Bibliotek Narodow (z zewntrz). Wstpimy do dawnych apartamentw cesarskich, Muzeum Sisi oraz Silberkammer, gdzie znajduje si unikalna kolekcja cesarskich sreber. Czas wolny na odpoczynek i kaw. Po dniu penym wrae przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Ten dzie powicimy na zwiedzanie Schnburnn- dawnej letniej rezydencji rodziny cesarskiej, ktra wzia swoj nazw od piknego rda, ktre odkryto tam w XVII w. Wystrj kadej komnaty zapiera dech rokokowym przepychem, misternym, kolorystycznym bogactwem dekoracji malarskiej i gszczem zoconych lici. Do cudw, jakie mona tam obejrze nale m.in.: Okrgy gabinet chiski, Wielka Galeria, Ukryta klatka schodowa, Niebieski salon Chiski. Budowla robi niesamowite wraenie ale uzupenia je wizyta w przepiknym ogrodzie, ktry rozciga si na tyach paacu i jest absolutnie bezkonkurencyjny we Wiedniu. Na 65 ha powierzchni znajduje si barokowy ogrd, obsadzony drzewami park, zwierzyniec, palmiarnia, kilka pawilonw, grot i architektonicznych wymysw. Ponadto fontanny, pomniki stojce wrd drzew i alejek. Nastpnie przejazd do Wiedeskiego Muzeum Historii Sztuki, gdzie zgromadzona zostaa jedna z najwikszych i najwspanialszych kolekcji dzie sztuki na wiecie, do ktrych zaliczamy m.in.: najwikszy zbr dzie Rubensa, bogat kolekcj prac Pietera Brueghla Starszego. Ponadto znajdziemy tam ptna takich artystw jak Rembrant, Tycjan, Caravaggio itd. Galeria obejmuje ogromn kolekcj przez lata gromadzon przez Habsburgw, rozlokowan w ponad stu pomieszczeniach na trzech poziomach. Po takiej dawce dozna artystyczno- historycznych relaksujcy spacer po ogrodach Burgartten, ktre zaoone zostay przez Habsburgw w centrum miasta na terenach otaczajcych Hofburg. Spacerujc wrd drzew, mija si pomniki Goethego, Mozarta, cesarza Franciszka. Park synie rwnie z secesyjnych oranerii zaprojektowanych przez Friedricha Ohmanna. W jednej z nich urzdzono Dom Motyli, gdzie w sztucznie stworzonych warunkach lasu tropikalnego yje ponad 150 gatunkw tych owadw. Po caodziennym zwiedzaniu udamy si do hotelu na obiadokolacj i nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Tego dnia pojedziemy zobaczy Belweder (z zewntrz);  Hundertwasserhaus  bajkowy dom, ktrego dachy poronite s dziesitkami drzew i krzeww tworzc urocze ogrody (z zewntrz). Nastpnie ponownie udamy si do zabytkowego i historycznego centrum Wiednia, tym razem na spacer do dzielnicy ydowskiej gdzie zobaczymy zabytkowe zabudowania ydowskiego Wiednia m.in. : Muzeum ydowskie, Mauzoleum Holokaustu, Stadttempel  jedyna we Wiedniu synagoga, cudem ocalona od zagady, stary ratusz - Altes Rathaus- z wynios barokow fasad; Nastpnie przejdziemy na Platz Am Hof otoczony XVI-XVII wieczn zabudow w ktr wkomponowany jest m.in. paac Collalto, w ktrym miao miejsce pierwsze publiczne wystpienie szecioletniego wwczas  W.A. Mozarta. Najwaniejsz atrakcj placu jest koci Am Hof (Dziewiciu Chrw Anielskich). Czas wolny na zakup pamitek bd wiedesk kaw. Przejazd do hotelu. Obiadokolacja. Nocleg</p>
                        </div>

                        <div className="fifth-day">
                            <h5>Dzie IV</h5>
                            <p>Wykwaterowanie z hotelu. Po niadaniu pojedziemy na wzgrze Kahlenberg, z ktrej krl Jan III Sobieski w 1683 roku dowodzi zwycisk bitw nad Turkami. Znajduje si tam kociek, w ktrym krl Polski modli si przed bitw oraz tablica powicona tym wydarzeniom. Oprcz tego ze wzgrza roztacza si przepikna panorama na Wiede. Po ostatnim spojrzeniu na stolic Austrii wyruszymy w drog powrotn do Polski.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Vienna;