import React from 'react';
import '../styles/program.scss';

import ItalyPilgrimageGallery from './ItalyPilgrimageGallery';

const ItalyPilgrimage = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Wochy</h1>
                    <h2>ladami Cudw Eucharystycznych</h2>
                    <h4>Ponad Eucharysti aden inny sakrament nie jest bardziej zbawienny; on usuwa grzechy, pomnaa cnoty, dusz nasyca obfitoci duchowych darw.
                       <span>w. Tomasz z Akwinu</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <ItalyPilgrimageGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki w godzinach nocnych dnia poprzedniego. Przejazd do Woch na nocleg w okolicach Padwy.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Przejazd do Padwy. Nawiedzenie bazyliki w. Antoniego. Modlitwa przy relikwiach. Nastpnie przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Przejazd doSieny miasta, w ktrym miay miejsce dwa cuda eucharystyczne. Zwiedzanie miejsc zwizanych z w. Katarzyn,  spacer po miecie.  Przejazd do Bolseny - miejsca cudu eucharystycznego ktry zapocztkowa wito Boego Ciaa. Nastpnie udamy si do niezwykego miasteczka Orvieto pooonego na szycie wulkanicznego wzgrza. Zwiedzimy tu jedn z najpikniejszych katedr gotyckich we Woszech, znajdujc si na placu Piazza del Duomo. KATEDRA w Orvieto kryje cenne relikwie: hosti i kapaski korpora. Przejazd do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie, przejazd doWatykanu: Bazylika w. Piotra  modlitwa przy grobie w. Jana Pawa II, Plac w. Piotra, zwiedzanieRzymubarokowego ze synnymi placami i fontannami: Placem Navona, Fontann di Trevi, Schodami Hiszpaskimi, oraz spacer do czci antycznej: Forum Romanum i Koloseum. Przejazd do hotelu, zakwaterowanie, obiadokolacja i nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>Po niadaniu przejazd do Rzymu. Zwiedzanie Rzymu Antycznego. Wyjazd doAlatri- miasta w ktrym wg. kocioa wydarzy si jeden z 4 najwaniejszych cudw eucharystycznych, wizyta w katedrze w. Pawa, Msza w. Nastpnie przejazd naMonte Cassino- cmentarz onierzy polskich oraz wizyta w klasztorze w. Benedykta. Przejazd do hotelu w okolicach Neapolu. Obiadokolacja i nocleg.</p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>Przejazd do Pompei. Nawiedzanie bazyliki Matki Boskiej Racowej. Nastpnie zwiedzanie Neapolu. Spacer po miecie z przewodnikiem, nawiedzenie katedry w. Januarego, gdzie znajduje si ampuka z krwi witego. W katedrze przynajmniej raz w roku obchodzi si cud w. Januarego, ktry polega na obserwacji krwi zamknitej w szklanej ampuce, ktra w dniu mczeskiej mierci w. Januarego przechodzi z postaci skrzepnitej w posta pynn. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>Wczesne niadanie. Przejazd do Lanciano - Msza w., zwiedzanie Sanktuarium Cudu Eucharystycznego. Nastpnie przejazd do Casci. Nawiedzenie sanktuarium w. Rity. Spacer uroczymi uliczkami miasta. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="eighth-day">
                            <h5>Dzie VIII</h5>
                            <p>niadanie. Przejazd do miasta w. Franciszka  do Asyu. Zwiedzanie Bazyliki w. Franciszka, modlitwa przy grobie witego. Czas wolny na spacer redniowiecznymi uliczkami miasta.  Nawiedzenie Bazyliki Matki Boej Anielskiej, ktra zbudowana zostaa w celu ochrony Porcjunkuli. Przejazd do Loreto. Nawiedzimy Sanktuarium w. Domku, bdcego nazaretaskim domem Maryi. Pomodlimy si rwnie na Cmentarzu Polskim za onierzy,, ktrzy polegli podczas II Wojny wiatowej w regionie Marche. Msza wita. Przejazd do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="ninth-day">
                            <h5>Dzie IX</h5>
                            <p>Po niadaniu wyjazd w kierunku pnocnych Woch. Nocleg tranzytowy.</p>
                        </div>

                        <div className="tenth-day">
                            <h5>Dzie X</h5>
                            <p>niadanie. Kontynuacja podry w kierunku Polski. Przyjazd do Polski w godzinach nocnych lub porannych dnia kolejnego (w zalenoci od miejsca zbirki).</p>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

export default ItalyPilgrimage;