import React from 'react';
import '../styles/program.scss';

import SpainBarcaGallery from './SpainBarcaGallery';

const Spain = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Hiszpania</h1>
                    <h2>Costa Brava i Rumba de Barcelona</h2>
                    <h4>Jedno serce, jedna ona, kataloska Barcelona. Jedno serce, jedno bicie, Barcelona ponad ycie.<span>przypiewka kibicw FC Barcelony</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <SpainBarcaGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z Polski. Przejazd w kierunku Woch. </p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>W godzinach porannych przyjazd w okolice Como.
                             <br />Como jest bajecznie pooon miejscowoci na granicy ze Szwajcari i nad jeziorem o tej samej nazwie. Jezioro znajduje si w Alpach i  jest trzecim co do wielkoci we Woszech. Jest najgbszym jeziorem Alp i jednym z najgbszych w Europie. Spacer po miecie z przewodnikiem- zwiedzanie Starwki, nastpnie czas wolny. Dla chtnych wyjazd kolejk na Brunate - punkt widokowy, z ktrego roztacza si pikny widok na jezioro i Alpy, rejs statkiem. Po poudniu wyjazd w kierunku Awinion.  Po drodze nocleg tranzytowy i obiadokolacja na terenie Woch. </p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Awinionu.
                            <br />Miasto papiey, pooone na brzegu Rodanu, pera  Prowansji, gdzie na kadym kroku czu zapach lawendy i zi. Spacer po miasteczku, podczas ktrego zobaczymy: synny most awinioski  Most w. Benezeta, Paac Papieski, katedr Notre Dame de Domes, a cae redniowieczne miasteczko zamknite jest w wietnie zachowanych murach obronnych- les Remparts. Czas wolny. Przejazd na obiadokolacj w restauracji flunch. Przejazd do hotelu. Zakwaterowanie. Nocleg.
                            </p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do lecego w sercu Pirenejw  Ksistwa Andory.
                            <br />Andora jest w czowce najmniejszych pastw na wiecie, synie z bajecznych widokw oraz z tego, e jest  jednym z rajw podatkowych, do ktrych nale m.in.: Monako, Bermudy, Kajmany, Bahamy, Emiraty Arabskie itd. Ze wzgldu na korzystne przepisy finansowe Andora to doskonae miejsce na zakupy. W okazjonalnych cenach naby mona takie produkty jak: alkohol, artkuy spoywcze, kosmetyki (w szczeglnoci perfumy), sprzt fotograficzny, elektroniczny. Czas wolny na zakupy. Nastpnie przejazd w kierunku Hiszpanii. Zakwaterowanie w hotelu na wybrzeu. Obiadokolacja. Nocleg.
                            </p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V - X</h5>
                            <p>Pobyt na Costa Brava.
                            <br />Odpoczynek, plaowanie, kpiele morskie, moliwo skorzystania z wycieczek fakultatywnych i licznych atrakcji, jakie oferuje Costa Brava. W wybranym przez pilota dniu odbdzie si wycieczka do Barcelony podczas ktrej zobaczymy: koci Sagrada Familia; synne kamienice Gaudiego Casa Mila i Casa Batllo; Stadion Camp Nou (dla chtnych wejcie), wzgrze Montjuc na ktrym oglda bdziemy obiekty zwizane z Wystaw wiatow z 1929 r., oraz z Olimpiad z 1992 r.;  Pomnik Krzysztofa Kolumba; synna La Rambla; Barri Gotic (Dzielnica Gotycka). Czas wolny. Powrt do hotelu, obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie XI</h5>
                            <p>niadanie. Wykwaterowanie. Wyjazd w kierunku Polski. </p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie XII</h5>
                            <p>Przyjazd do Polski. Godziny uzalenione od miejsca zbirki</p>
                        </div>

                        <div className="propose-trip">
                            <h5>Proponowane wycieczki fakultatywne</h5>
                            <div>
                                <h6>Montserrat</h6>
                                <p>Niezwyky masyw grski, w ktrym znajduje si klasztor benedyktyski oraz bazylika Najwitszej Marii Panny, w ktrej najwaniejszym miejscem jest komnata Czarnej Madonny  patronki Katalonii, zwanej pieszczotliwie przez Kataloczykw Czarnulk. Ponadto gry oferuj takie atrakcje jak kolejki, szlaki grskie, punkty widokowe, z ktrych roztaczaj si cudowne widoki. Wspania pamitk z Montserrat s wyrabiane wg benedyktyskich receptur  likiery, sery, miody, zioa itd.</p>
                            </div>
                            <div>
                                <h6>Figueres i Girona</h6>
                                <p>W Figueres znajduje si Muzeum Salvadora Dal- obok madryckiego Prado, najczciej odwiedzane muzeum w Hiszpanii. W Gironie natomiast zobaczymy doskonale zachowane stare miasto nad ktrym gruje monumentalna katedra. Ponadto zobaczymy anie arabskie, mury miejskie, koci w. Feliksa.</p>
                            </div>
                            <div>
                                <h6>Tossa de Mar i wizyta w winiarni</h6>
                                <p>Najpikniejsze i najbardziej romantyczne miasteczko na wybrzeu Costa Brava</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

export default Spain;