import React from 'react';
import '../styles/program.scss';

import PortugalGallery from './PortugalGallery';

const Portugal = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Portugalia</h1>
                    <h2>Azulejos, sardynki i wielkie odkrycia</h2>
                    <h4> Nie znam kwiatw o podobnej rnorodnoci kolorw jak Lizbona w socu
                       <span>Fernando Pesoa</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <PortugalGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wylot z Polski. Przylot do Lizbony. Przejazd do hotelu w okolicach Figueira da Foz. Zakwaterowanie w hotelu. Nocleg. </p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Czas wolny na odpoczynek, pla. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>Wycieczka do Lizbony. Caodzienne zwiedzanie Lizbony z przewodnikiem.  W programie: Dolne Miasto (Baixa) gdzie zobaczymy: Plac Restauradores, Plac Rossio, Rua Augusta, Praa de Comrico i wejdziemy na Bairro Alto. Przejcie spacerem na Alfam. Zobaczymy lizbosk katedr oraz koci w. Antoniego. Przejazd autokarem do dzielnicy Belem. Nastpnie zwiedzanie XVI-wiecznej dzielnicy Belm, gdzie zobaczymy Klasztor Hierominitw i przylegajcy do niego koci Najwitszej Marii Panny, wie Belem i Pomnik Odkrywcw. Dodatkowa atrakcja w Belm: pyszne ciasteczka z Belem, jedyne takie w Portugalii.  Nastpnie przejazd do Sintry. Spacer z przewodnikiem po niezwykym kolorowym miasteczku syncym z niesamowitych paacw: Quinta da Regaleira to ekstrawagancka gotycka posiado z XIX wieku, Paac Pena, Zamek Maurw to czciowo zrekonstruowana forteca datowana na IX wiek, wybudowana przez Maurw z pnocnej Afryki w celu ochrony yznych terenw Sintry a przede wszystkim Palacio da Pena to niezwykle kolorowy i misternie zdobiony paac, stanowicy gwn atrakcj kadej wycieczki do Sintry. Powrt do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Dzie wolny.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>niadanie. Przejazd na zwiedzanie Coimbry i Fatimy.
                            W Coimbrze bardzo atwo zakocha - w jej architekturze, kulturze, historii. Ulubione miasto studentw, gdzie kultywuje si dawne tradycje ycia studenckiego. To miasto narodzin szeciu krlw Portugalii, a take siedziba najstarszego w kraju uniwersytetu. Coimbra bya rwnie pierwsz stolic Portugalii, a do dnia dzisiejszego miasto to wzbudza w Portugalczykach szacunek i sentyment. Nastpnie udamy si do Fatimy. Nawiedzenie sanktuarium fatimskiego i miejsc zwizanych z objawieniem Matki Boskiej trojgu pastuszkom. Powrt do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>niadanie. Przejazd do Porto. Jest to drugie miasto co do wielkoci po Lizbonie. Zobaczymy: Katedr w Porto, renesansowy koci Santa Klara, mury Ferdynanda,  zabytkowy dworzec Sao Bento, dzielnic Riberia, ktra oddaje ducha Portugali, ratusz dominujcy na pnocnym kocu Avienda dos Aliados, degustacja win. Powrt do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Wykwaterowanie. transfer na lotnisko. Wylot do Polski.</p>
                        </div>

                    </div>
                </div>
            </section>

        </>
    );
}

export default Portugal;