import React from 'react';
import '../styles/program.scss';
import LodzGallery from './LodzGallery';

const Lodz = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>d</h1>
                    <h2>HOLLYD CZY FASHION D</h2>
                    <h4>Kiedy tylko bywam w odzi, nie mog nie by na Piotrkowskiej. Tam si wszystko dzieje, tam si wszystkich spotyka.
                       <span>Micha Urbaniak</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <LodzGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd w godzinach wczesno-porannych. Przyjazd do odzi i zwiedzanie miasta z przewodnikiem: Manufaktura, synna ulica Piotrkowska, Ksiy Myn- miasto w miecie wzorowane na angielskich osadach przemysowych. Znajdoway si tam budynki fabryczne m.in. ogromna przdzalnia, magazyny, famuy, szkoa, remiza stray ogniowej, dwa szpitale, gazownia, klub fabryczny, sklepy, rezydencje wacicieli, a take bocznica kolejowa. Zesp urbanistyczny Ksiy Myn to najwikszy zabytkowy kompleks fabryczny odzi, powstay w drugiej poowie XIX wieku. Tego dnia zobaczymy rwnie Muzeum Miasta odzi w paacu Izraela Poznaskiego  dzki Luwr oraz inne kamienice i paace dzkich fabrykantw. Przejazd do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>Tego dnia odwiedzimy Wytwrni Filmw Owiatowych, gdzie wejdziemy w rol filmowcw, reyserw, scenarzystw i aktorw, i zrealizujemy wasny film.  Nastpnie udamy si na zwiedzanie Muzeum Wkiennictwa oraz udzia w warsztatach muzealnych. Ostatnim punktem programu tego dnia bdzie wizyta w Centrum naukowym EC1.  Centrum powstao w budynku pierwszej elektrowni miejskiej. W styczniu 2018 roku zostao otwarte interaktywne Centrum Nauki i Techniki - najwiksza pod wzgldem powierzchni tego typu placwka w kraju. Na prawie 8,5 tys. mkw. powierzchni wystawienniczej w EC1- Zachd stany trzy cieki edukacyjne. Pierwsza z nich dotyczy przetwarzania energii, druga rozwoju wiedzy i cywilizacji, a trzecia pozwala zgbi tajemnice mikro- i makrowiata.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, wykwaterowanie.  Przejazd do Muzeum Fabryki  w dzkiej Manufakturze. Warsztaty muzealne. Po warsztatach obiad. Czas wolny na zakupy. Wyjazd w kierunku miejsca zbirki. </p>
                        </div>

                    </div>
                </div>
            </section>

        </>
    );
}

export default Lodz;