import React from 'react';
import '../styles/OurTeam.scss';
import Pulse from 'react-reveal/Pulse';
import Zoom from 'react-reveal/Zoom';
import Flip from 'react-reveal/Flip';
import Fade from 'react-reveal/Fade';

import anna from '../img/annaG.jpg';
import maja from '../img/maja.jpg';
import marta from '../img/marta.jpg';
import spirek from '../img/spirek.jpg';
import monika from '../img/monika.jpg';
import ola from '../img/ola.jpg';



const OurTeam = () => {
    return (
        <>
            <section className="our-team" id="ourTeam">

                <Pulse><h1>Poznaj nasz zesp</h1></Pulse>
                <Zoom left><h3>Kiedy zaapiesz bakcyla podrowania, nie ma na to adnego lekarstwa. Ju wiem, e bd szczliwie chory do koca ycia. <span> Michael Palins</span></h3></Zoom>

                <div>
                    <div className="anna">
                        <div>
                            <div><img src={anna} alt="" /></div>
                            <Flip bottom><h4>Anna Gajewska</h4></Flip>
                        </div>
                        <Fade><p>Moje serce jest podzielone pomidzy Francj, Hiszpani i Portugali i tam spdzam poow swojego ycia. To moje ukochane kierunki i zawsze jestem niezmiernie szczliwa, gdy mog Pastwa tam zabra i opowiada  o wspaniaociach, ktre napotykamy podczas naszej podry. Uwielbiam rwnie soneczn Itali i bardzo prawdopodobne, e rwnie tam si zobaczymy. Moja specjalizacja to wyjazdy pielgrzymkowe. W biurze natomiast jestem odpowiedzialna  za kontakty z Pastwem. Ukadam programy, dokonuj rezerwacji i dokadam wszelkim stara, aby nasza wsplna podr bya pena wrae.</p></Fade >
                    </div>

                    <div className="maja-marta">
                        <div className="centre">

                            <div>
                                <div><img src={maja} alt="" /></div>
                                <div><img src={marta} alt="" /></div>
                            </div>
                            <Flip top><h4>Maja i Marta - Lisban Story</h4></Flip>
                        </div>
                        <Fade><p>To One opowiedz nam o Portugalii i zadbaj o nasz udany pobyt. Pod ich dowdztwem wsidziemy na pokad portugalskiej karaweli i opyniemy wiat ladami Ferdynanda Magellana, Bartolomea Diasa, Vasco da Gamy. Poznamy histori wielkich odkry geograficznych, przeniesiemy si do czasw Manuela I Szczliwego za ktrego panowania Portugalia przeywaa swj zoty wiek. Wsplnie odwiedzimy Dzielnic Betlejemsk, magiczne zauki Alfamy oraz udamy si na fantastyczny lunch. Maja i Marta znaj najlepsze adresy w Lizbonie, gdzie zjemy wymienitego bacalhau, caldeirada lub po prostu grillowane sardynki, ktrych zapach w porze obiadu roznosi si po caej Lizbonie. Bdzie rwnie czas na deser brzuch zakonnicy, skrzyda anioa, bekon z nieba lub synne ciasteczka betlejemskie. Lizbona w towarzystwie dziewczyn to wspaniale spdzony czas i niezapomniane przeycia.</p></Fade>
                    </div>

                    <div className="spirek right">
                        <div>
                            <div><img src={spirek} alt="" /></div>
                            <Flip bottom><h4>Spirek</h4></Flip>
                        </div>
                        <Fade><p>Mj anio str we Francji. Czuwa nad kadym moim krokiem tam postawionym. Super kolega, ostoja spokoju, wietny doradca, a przede wszystkim wspaniay przewodnik po Francji. W niezwykle ciekawy sposb przeprowadzi nas przez dzieje tego kraju, opowie o pikantnych szczegach z ycia francuskich krlw, romansach i aferach dworskich, wojnach. Poprowadzi nas przez sale najwikszego muzeum na wiecie - on o Luwrze wie wszystko. Artystyczna dusza, nie lubi podych Anglikw, mionik dobrej kuchni i propagator sztuki jedzenia. Kiedy usyszaam e jestem barbarzyc bo le kroj ser.</p></Fade>
                    </div>

                    <div className="monika">
                        <div>
                            <div><img src={monika} alt="" /></div>
                            <Flip top><h4>Monika Skrzypek</h4></Flip>
                        </div>
                        <Fade><p>Mj doradca personalny, zawodowy i analityk. Pilot wycieczek z prawdziwego zdarzenia z podejciem do ludzi, yczliwoci i umiechem. Zazdroszcz jej cierpliwoci, byskotliwego dowcipu i ciepego gosu, ktrego chce si sucha bez koca (nieoceniona cecha pilota wycieczek). Monia zajmie si Wami w Hiszpanii, we Francji, na Sycylii, na nartach. Uwielbia dzieci i z wielk pasj prowadzi wycieczki szkolne. Podrowanie w Jej towarzystwie to wielka przyjemno i poczucie bezpieczestwa. Ponadto nadzoruje sprawy biurowe, pilnuje rezerwacji, tworzy dla Pastwa programy.</p></Fade>
                    </div>

                    <div className="ola right">
                        <div>
                            <div><img src={ola} alt="" /></div>
                            <Flip bottom><h4>Ola</h4></Flip>
                        </div>
                        <Fade><p>Znakomita pilotka, specjalistka od Grecji oraz opiekunka od spraw urzdowych, prawnych i administracyjnych. Ogromne wsparcie, dobra rada, anielska cierpliwo. O czarujcym glosie ju wspominaam, ale w przypadku Oli nie da si tego pomin. Swoim ciepym i yczliwym tonem opowie Wam o Grecji, w ktrej jest zakochana.  Uwielbia dzieci w zwizku z tym zajmuje si organizacj wycieczek szkolnych i jest z tego powodu bardzo Happy.</p></Fade>
                    </div>

                </div>


            </section>
        </>
    );
}

export default OurTeam;