import React from 'react';
import '../../styles/program.scss';
import RomeGallery from './RomeGallery';

const Rome = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Antyczny Rzym</h1>
                    <h4>BRAK OPISU!!!!!!!!!!!!!!<span>!!!!!!!!!!!!!</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <RomeGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka na lotnisku, odprawa, wylot. Ldowanie na lotnisku w Rzymie. Transfer do hotelu, obiadokolacja, nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie, wyjazd na zwiedzanie Rzymu autokarem. W programie: Watykan - wntrze bazyliki w. Piotra i plac w. Piotra. Wizyta w miejscu spoczynku Jana Pawa II. Czas wolny na obiad i zakupy. Nastpnie spacer po Rzymie barokowym, podczas ktrego zobaczymy m.in.: Piazza Navona, Panteon antyczn wityni wszystkich bogw, mieszczc grb Rafaela, Fontann di Trevi, Piazza del Popolo  symetrycznie zabudowany olbrzymi plac, z ktrego wida w perspektywie ca Via del Corso a po pomnik Wiktora Emanuela II. Zobaczymy rwnie Piazza Spaga ze synnymi Schodami Hiszpaskimi. Powrt do hotelu, obiadokolacja, nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, przyjazd na dalsze zwiedzanie Rzymu. Tego dnia bdziemy zwiedza Rzym antyczny - fora cesarskie z kolumn Trajana, Koloseum, Piazza Venezzia z monumentalnym Pomnikiem Wiktora Emanuela II, zwanym Vittoriano. Nastpnie zwiedzanie bazylik: San Giovanni in Laterano, Santa Maria Maggiore i San Pietro in Vincolo, gdzie podziwia mona synny posg Mojesza duta Michaa Anioa. Czas wolny. Powrt do hotelu, obiadokolacja, nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie, wykwaterowanie, przejazd autokarem na lotnisko. Wylot do Polski.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Rome;