import React from 'react';
import '../styles/Pilgrimage.scss';
import Fade from 'react-reveal/Fade';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import italy from '../img/pielgrzymka-glowna.jpg';
import holyLandImg from '../img/holy-land.jpg';
import mary from '../img/mary.jpg';

import ItalyPilgrimage from './ItalyPilgrimage';
import HolyLand from './HolyLand';
import FootstepsMary from './FootstepsMary';



const Pilgrimage = () => {
    return (
        <Router>

            <Route path="/italyPilgrimage" component={ItalyPilgrimage} />
            <Route path="/holyLand" component={HolyLand} />
            <Route path="/footstepsMary" component={FootstepsMary} />


            <section className="section-pilgrimage" id="pilgrimage">
                <Fade right cascade><h1>Pielgrzymki</h1></Fade>
                <Fade right cascade><div>
                    <div className="pilgrimage-italy">
                        <div className="pilgrimage-photo">
                            <img src={italy} alt="" />
                        </div>
                        <h2>Wochy</h2>
                        <h3>ladami cudw eucharystycznych</h3>
                        <p>Ponad Eucharysti aden inny sakrament nie jest bardziej zbawienny; on usuwa grzechy, pomnaa cnoty, dusz nasyca obfitoci duchowych darw.<span>w. Tomasz z Akwinu</span></p>
                        <button>
                            <Link to="/italyPilgrimage">czytaj wicej</Link>
                        </button>
                    </div>

                    <div className="holy-land">
                        <div className="pilgrimage-photo">
                            <img src={holyLandImg} alt="" />
                        </div>
                        <h2>Ziemia wita</h2>
                        <h3>Szlakiem biblijnym</h3>
                        <p>Ach, miejsce ziemi, miejsce ziemi witej  jakime miejscem jeste we mnie! Dlatego wanie nie mog po tobie stpa, musz klkn<span>Karol Wojtya</span></p>

                        <button>
                            <Link to="/holyLand">czytaj wicej</Link>
                        </button>
                    </div>

                    <div className="footsteps-mary">
                        <div className="pilgrimage-photo">
                            <img src={mary} alt="" />
                        </div>
                        <h2>Francja Hiszpania Portugalia</h2>
                        <h3>ladami sanktuariw Maryjnych</h3>
                        <p>Maryja sama szuka takich, ktrzy by do Niej pobonie i z uszanowaniem przystpowali: tych bowiem miuje, tych karmi i zasila, tych za dzieci przyjmuje.<span>w. Bonawentura</span></p>

                        <button>
                            <Link to="/footstepsMary">czytaj wicej</Link>
                        </button>
                    </div>
                </div></Fade>
            </section>
        </Router>
    );
}

export default Pilgrimage;