import React from 'react';
import '../styles/program.scss';
import SeaGallery from './SeaGallery';

const Sea = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>BATYK</h1>
                    <h2>Moe nasze morze?</h2>
                    <h4>Opis wymyl
                       <span>Anna Gajewska Tekla</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <SeaGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka w godzinach wczesno-porannych.  Wyjazd w kierunku Gdaska. Przyjazd na Westerplatte. Spacer z przewodnikiem Szlakiem Turystycznym "Westerplatte" podczas ktrego poznamy histori obrony Westerplatte, zobaczymy teren i obiekty byej Wojskowej Skadnicy Tranzytowej. Nastpnie przejazd do Gwnego Miasta. Spacer Drog Krlewsk.  Zobaczymy takie zabytki jak: Zielona Brama, Krlewskie Kamieniczki, Dwr Artusa, Fontanna Neptuna, Ratusz Gwnego Miasta, Dom Uphagena, Zota Brama, Wiea Wizienna, Katowania, Brama Wyynna.  Zwiedzanie Bazylika Mariackiej i spacer niezwykle urokliw ulic Mariack. Zobaczymy rwnie synnego urawia - zabytkowy dwig portowy. Spacer Dugim Pobrzeem. Czas wolny.  Przejazd do Sopotu. Spacer reprezentacyjn alej Bohaterw Monte Cassino. Wejcie na najdusze w Europie drewniane molo.   Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>Po niadaniu przejazd do Centrum Nauki Experyment w Gdyni. Centrum jest niecodzienn pracowni dowiadczaln dla maych i duych odkrywcw  amatorw, w ktrym w sposb interaktywny mog na wasn rk przeprowadzi eksperymenty, poznajc prawa zachodzce w przyrodzie. Nastpnie udamy si na spacer po Skwerze Kociuszki, gdzie zobaczymy m.in. polski niszczyciel ORP Byskawica oraz najsynniejszy polski aglowiec- Dar Pomorza.  Na skwerze znajduje si rwnie Akwarium Morskie oraz pomniki: Maszty oraz Josepha Conrada- synnego angielskiego pisarza, polskiego pochodzenia.  Tego dnia odwiedzimy Muzeum Emigracji - pierwsze w kraju muzeum powicone historii polskiej emigracji, powstae w historycznym gmachu Dworca Morskiego, skd przez dziesiciolecia wypyway polskie transatlantyki.  Po wizycie w muzeum przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd na zwiedzanie Muzeum Wsi Sowiskiej w Klukach. Obejrzymy tam zrekonstruowan wie, ktr zamieszkiwali kiedy Sowicy  (zachodniopomorscy Kaszubi). W muzeum prezentowana jest tradycja i kultura typowa dla przyjeziornych wsi rybackich. Znajduj si tam zarwno obiekty oryginalne starej osady, jak  i zrekonstruowane.  Po zwiedzaniu przejazd do eby. Wejcie do Sowiskiego Parku Narodowego, w ktrym podziwia bdziemy ruchome wydmy, bdce unikatowym zjawiskiem przyrodniczym parku. Po wizycie w parku, wyjazd w drog powrotn do domu. Po drodze przerwa na obiad lub obiadokolacj.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Sea;