import React, { Component } from 'react';
import '../styles/WhyUs.scss';
import Pulse from 'react-reveal/Pulse';
import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide';

import eiffla from '../img/eiffla.jpg';
import beach from '../img/church.jpg';
import place from '../img/place.png';
import adventure from '../img/adventure.png';
import unforgettable from '../img/unforgettable.png';


class WhyUs extends Component {
    state = {
        quality: true,
        need: false,
        experience: false,
    }

    handleChangeQuality = () => {
        let need = this.state.need;
        let experience = this.state.experience;

        if (need === true || experience === true) {
            need = false;
            experience = false
        }
        this.setState({
            need,
            experience,
            quality: !this.state.quality
        })
    }

    handleChangeNeed = () => {
        let quality = this.state.quality;
        let experience = this.state.experience;

        if (quality === true || experience === true) {
            quality = false;
            experience = false
        }
        this.setState({
            quality,
            experience,
            need: !this.state.need
        })
    }

    handleChangeExperience = () => {
        let quality = this.state.quality;
        let need = this.state.need;

        if (quality === true || need === true) {
            quality = false;
            need = false
        }

        this.setState({
            quality,
            need,
            experience: !this.state.experience
        })
    }

    render() {
        return (
            <>
                <section className="whyUs" id="whyUs">

                    <div>
                        <div className="whyUs-photos">
                            <img className="eiffla" src={eiffla} alt="wiea Eiffla" />
                            <img className="beach" src={beach} alt="Plaa" />
                        </div>

                        <Fade right cascade><div className="advantage">
                            <Pulse><h1>Nasze zalety</h1></Pulse>
                            <Fade right cascade><div>
                                <div className={this.state.quality ? "arrow active" : "arrow"} onClick={this.handleChangeQuality}>
                                    <div><i class="fas fa-chevron-up"></i></div>
                                    <h4> najwysza jako usug</h4>
                                </div>
                                <p className={this.state.quality ? "active" : null}>wiadczymy usugi na najwyszym poziomie. Wsppracujemy z najlepszymi kontrahentami, hotelami, pilotami oraz przewodnikami. Wszystkie czynniki, majce wpyw na nasze usugi dobierane s z najwiksz starannoci i dbaoci o kady detal. Nad planem Pastwa wyprawy, pracuj dowiadczeni profesjonalici, ktrzy wkadaj cae serce, aby Pastwa podr bya udana. </p>
                            </div></Fade>

                            <div>
                                <div className={this.state.need ? "arrow active" : "arrow"} onClick={this.handleChangeNeed}>
                                    <div><i class="fas fa-chevron-up"></i></div>
                                    <h4>rozumiemy potrzeby klientw/a</h4>
                                </div>
                                <p className={this.state.need ? "active" : null}>Zesp Travel Story tworz ludzie zajmujcy si turystyk od zawsze. Od wielu lat pracujemy z ludmi, obsugujemy grupy i lata pracy pozwoliy nam dokadnie pozna i zrozumie potrzeby naszych Klientw. Nasze programy ukadamy w taki sposb, aby z kadego dnia wycisn jak najwicej, eby pokaza Pastwu wszystko co najwaniejsze, ale eby nie zabrako rwnie czasu na prywatne obserwacje, zjedzenie dobrego lunchu czy wypicie aromatycznej kawy w przytulnej kawiarence. Jestemy elastyczni i potrafimy sucha. Jestemy otwarci na Pastwa propozycje. Licz si dla nas ludzie, a nie, zaliczone punkty. Sami jestemy podrnikami i doskonale wiemy z czym to si je. </p>
                            </div>

                            <div>
                                <div className={this.state.experience ? "arrow active" : "arrow"} onClick={this.handleChangeExperience}>
                                    <div><i class="fas fa-chevron-up"></i></div>
                                    <h4>dowiadczona kadra</h4>
                                </div>
                                <p className={this.state.experience ? "active" : null}>Travel Story powstao z wielkiej pasji do podry. Od zawsze podrowalimy po najrniejszych zaktach Europy i wiata. Miejsca, w ktre Pastwa zabieramy, odwiedzilimy wielokrotnie i doskonale je znamy. Wiemy, co warto zobaczy, gdzie zje, co kupi. Mamy wieloletnie dowiadczenie w planowaniu i organizacji podry, oraz w ich pilotowaniu. Zabieramy Pastwa w miejsca, ktre kochamy, czasami traktujemy jak drugi dom, dzielimy si z Pastwem nasz pasj. Mimo naszego dowiadczenia, nigdy nie dziaamy schematycznie. I chobymy jechali w dane miejsce setny raz, to kada podr jest dla nas nowym wyzwaniem i wspaniaym przeyciem. </p>
                            </div>
                        </div></Fade>

                    </div>

                    <Slide bottom cascade><div className="boxes">
                        <div>
                            <img className="whyUs-icon" src={place} alt=""></img>
                            <div>
                                <h4>niesamowite miejsca</h4>
                                <p>Lorem ipsum dolor sit amet consectetur </p>
                            </div>
                        </div>
                        <div>
                            <img className="whyUs-icon" src={adventure} alt=""></img>
                            <div>
                                <h4>wspaniae przygody</h4>
                                <p>Lorem ipsum dolor sit amet</p>
                            </div>
                        </div>
                        <div>
                            <img className="whyUs-icon" src={unforgettable} alt=""></img>
                            <div>
                                <h4>niezapomniane wraenia</h4>
                                <p>Lorem ipsum dolor sit amet cons</p>
                            </div>
                        </div>
                    </div></Slide>
                </section>
            </>
        );
    }
}

export default WhyUs;