import React from 'react';
import '../styles/program.scss';

import HolyLandGallery from './HolyLandGallery';

const HolyLand = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Ziemia wita</h1>
                    <h2>Szlakiem biblijnym</h2>
                    <h4>Ach, miejsce ziemi, miejsce ziemi witej  jakime miejscem jeste we mnie! Dlatego wanie nie mog po tobie stpa, musz klkn
                       <span>Karol Wojtya</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <HolyLandGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka na lotnisku w hali odlotw, odprawa bagaowo  biletowa, wylot do Tel Awiwu. Po wyjciu z lotniska przejazd do Betlejem na zakwaterowanie, kolacj oraz nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie, wykwaterowanie i wyjazd w kierunku Morza rdziemnego do Cezarei Nadmorskiej, gdzie zobaczymy ruiny staroytnych akweduktw. Przejazd do Hajfy, pooonej u stp Gry Karmel - nawiedzenie Kocioa Stella Maris wraz z Grot proroka Eliasza. Przejazd do Nazaretu, gdzie Archanio Gabriel oznajmi Maryi radosn wie, e zostaa wybrana przez Boga na matk Jego Syna. Nawiedzenie Bazyliki Zwiastowania z domem rodzinnym NMP i w. Jzefa. Przejazd do Kany Galilejskiej,  w ktrej nawiedzimy Sanktuarium Pierwszego Cudu Pana Jezusa. Moliwo odnowienia przyrzecze maeskich. Przejazd na obiadokolacj i nocleg do hotelu w Nazarecie/Tyberiadzie.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>Przejazd w kierunku Gry Tabor. Na jej szczycie, znajduje si Bazylika Przemienienia Paskiego. Nastpnie udamy si nad Jezioro Galilejskie, w otoczeniu ktrego znajduje si Gra 8 Bogosawiestw, ktra urzeka piknem przyrody i spokojem. Przejazd do Tabghi, gdzie nawiedzimy Koci Rozmnoenia chleba i ryb,  upamitniajcy cudowne rozmnoenie poywienia przez Pana Jezusa i nakarmienie 5 tysicy mczyzn nie liczc kobiet i dzieci. Przejcie do Kocioa Prymatu, miejsca gdzie Pan Jezus nada prymat w. Piotrowi sowami pa baranki moje, pa owce moje. Dzi znajduje si tu niewielki Koci wybudowany w XX wieku z czarnego bazaltu, w ktrym zobaczy mona kamienny st, przy ktrym Zmartwychwstay spoy niadanie wraz z Apostoami. Przejazd do lokalnej restauracji na lunch  ryb w. Piotra (opcjonalnie 20$). Nastpnie przejazd do Kafarnaum - miasta wymienianego w Ewangeliach najczciej po Jerozolimie. Dzi w Kafarnaum zobaczy mona pozostaoci po domu w. Piotra, osiedlach mieszkalnych oraz pozostaoci po tzw. Biaej Synagodze, wybudowanej na fundamentach tej, w ktrej naucza Jezus. Rejs odzi po Jeziorze Galilejskim, z perspektywy, ktrego zobaczy mona obszar dziaalnoci Pana Jezusa. Powrt do hotelu do hotelu w Betlejem, zakwaterowanie, kolacja i nocleg. </p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Przejazd na Pole Pasterzy, miejsca gdzie Anio Pasterzom obwieszcza nowin o Narodzeniu Jezusa. Przejazd do sklepu z dewocjonaliami, z moliwoci zakupu pamitek. Sklep prowadzony jest od lat przez palestyskich Chrzecijan. Nawiedzenie Bazyliki Narodzenia Paskiego  Grota Narodzenia.  Wizyta w Kociele w. Katarzyny. Przejcie do Groty Mlecznej  miejsce, gdzie najwitsza Maryja Panna karmic Jezusa uronia kropl mleka i grota staa si caa biaa. Popoudniu dzie w Ain Karem. Wizyta w Kociele Nawiedzenia w. Elbiety. Przejcie do miejsca narodzin Jana Chrzciciela. Powrt do hotelu w Betlejem. Kolacja i nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>niadanie. Przejazd na Gr Oliwn. Wizyta w Betfage  pocztek tradycyjnej procesji Niedzieli Palmowej. Koci Pater Noster, gdzie znajduj si majolikowe tablice, upamitniajce modlitw, ktrej nauczy nas Pan Jezus  Ojcze Nasz.  Przejcie na punkt widokowy na Grze Oliwnej. Zejcie do kocioa Dominus Flevit miejsca, gdzie Pan zapaka nad Jerozolim. Wizyta w Ogrodzie Oliwnym wspomnimy modlitw Chrystusa   w Getsemani  w Ogrjcu. Nastpnie udamy si na Gr Syjon, by nawiedzi koci Zanicia NMP. Nawiedzimy te Wieczernik, gdzie Pan Jezus wraz z Apostoami spoy Ostatni Wieczerz, podczas ktrej ustanowi Sakrament Eucharystii i Kapastwa. Wejcie do synagogi, w ktrej znajduje si symboliczny Grb Krla Dawida. Powrt do hotelu w Betlejem. Kolacja i nocleg.</p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>niadanie. Wyjazd w kierunku Pustyni Judzkiej do Betanii, nawiedzenie Sanktuarium Domu Przyjaci Pana Jezusa Marii, Marty i azarza. Przejazd do Jerycha, nazywanego miastem palm daktylowych, pod Gr Kuszenia oraz wspomnienie Zacheusza  poborcy podatkowego przy tzw. Sykomorze Zacheusza. Przejazd nad rzek Jordan do miejsca, gdzie Chrzecijanie z caego wiata odnawiaj przyrzeczenia chrzcielne. Przejazd nad Morze Martwe i czas wolny na relaksujc kpiel. Powrt do hotelu w Betlejem na obiadokolacj i nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Wejcie do Starego Miasta w Jerozolimie. Przejcie do Kocioa w. Anny, miejsca domu rodzicw Matki Boskiej  Joachima oraz Anny. Obok wspomnienie cudu Pana Jezusa uzdrowienia chorego przy Sadzawce Betesda, dalej przez Stare Miasto do miejsca wczesnej warowni Marka Antoniusza, gdzie znajduj si dwie pierwsze stacje Drogi Krzyowej. Rozpoczcie naboestwa Drogi Krzyowej, przejcie Via Dolorosa do Bazyliki Mki Paskiej. Wejcie na Golgot. Zakoczenie Drogi Krzyowej przy Grobie Paskim. Zwiedzanie Bazyliki. Czas wolny na zakupy na targu jerozolimskim. Spacer przez dzielnic ydowsk i ormiask pod cian Paczu. Powrt do hotelu w Betlejem. Kolacja i nocleg. </p>
                        </div>

                        <div className="eighth-day">
                            <h5>Dzie VIII</h5>
                            <p>niadanie, wykwaterowanie z hotelu. Transfer na lotnisko w Tel Awiwie. Wylot do Polski.</p>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

export default HolyLand;