import React from 'react';
import '../../styles/program.scss';
import PragueGallery from './PragueGallery';

const Prague = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Lunatyczna Praga</h1>
                    <h4>Praga nie puszcza nikogo z tych, ktrych pochwycia<span>Angelo Maria Ripellino</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <PragueGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka. Wyjazd w kierunku Czech. Przyjazd do Pragi. Spacer po miecie: Maa Starna- klimatyczna dzielnica u stp Hradczan z urokliwymi kamieniczkami, uliczkami, barokowymi paacami. Zobaczymy rwnie klasztor na Strahowie, kompleks barokowych budynkw sakralnych- Loret. Odwiedzimy przepikn bibliotek na Strahovie. Wstpimy rwnie do Muzeum Miniatur, prezentujce niesamowit kolekcj figurek. Niektre z nich mona oglda tylko przez soczewki powikszajce. Zobaczymy m.in. Wie Eiffla, ktra jest 100 tys. Razy mniejsza od oryginau. Przejazd do hotelu. Zakwaterowanie. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Spacer po gwnym placu miejskim- Rynku Staromiejskim przy ktrym ulokowany jest Ratusz Staromiejski ze synnym zegarem astronomicznym Orloj, pomnik Jana Husa. Wejcie do Muzeum Figur Woskowych  oraz Muzeum Czekolady, gdzie zobaczymy ekspozycj historii czekolady, poznamy histori picia kakao oraz czekolady, sigajc mitycznych Aztekw, ku naszemu zadowoleniu utwierdzimy si w przekonaniu, e czekolada,  to cakiem zdrowa przekska . Bdzie oczywicie moliwo degustacji czekoladek, trufli i pralin czekoladowych. Kolejn atrakcj bdzie pokaz przygotowania absyntu wraz z degustacj. Czechy s jednym z nielicznych krajw, w ktrych absynt jest legalny i prbujc tzw. zielonej wrki poczujemy si jak prawdziwi artyci.
                            Wieczorny rejs po Wetawie z kolacja i 1 piwem. Powrt do hotelu. Nocleg.
                            </p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Przejazd do Velkich Popowic, gdzie bdziemy zwiedza browar. Usyszymy histori browaru, poznamy procesy waenia piwa, zwiedzimy zabytkow warzelni , sprbujemy wieo uwaonego piwa, obejrzymy pakowni oraz wystaw zabytkowych opakowa do piwa. Powrt do centrum. Zwiedzanie krlewskiej dzielnicy na zachodnim brzegu Wetawy- na Hradczany, gdzie zobaczymy m.in. Zamek Krlewski, Zot Uliczk, Katedr w. Czas wolny. Wieczorem lokalna kolacja w jednym z praskich lokali z piwem, winem i pokazem folklorystycznym. Powrt do hotelu. Nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Przejazd kolejk na wzgrze Petrin. Nie ma pikniejszego widoku na Prag, ni ten ze wzgrza na Petrinie. Jest dla Pragi tym czym Montmartre dla Parya, Janikulum dla Rzymu czy Gra Gellerta dla Budapesztu. Dla chtnych wejcie na mini wie Eiffla, bd do Gabinetu Krzywych zwierciade (dodatkowo patne). Ostatni rzut oka na Prag. Wyjazd autokarem w kierunku Krakowa. Przyjazd w godzinach pnowieczornych.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Prague;