import React from 'react';
import '../styles/program.scss';
import PiastowieGallery from './PiastowieGallery';

const Piastowie = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Na Szlaku Piastowskim</h1>
                    <h2>Na Szlaku Piastowskim</h2>
                    <h4>opis..............................
                       <span> autor</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <PiastowieGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka i wyjazd w godzinach wczesno-porannych. Przyjazd do Gniezna. Zwiedzanie gotyckiej archikatedry z XIV wieku, w ktrej znajduj si relikwie ze szcztkami w. Wojciecha oraz synne drzwi gnienieskie , na ktrych przedstawiono ycie w. Wojciecha. Jest to najcenniejszy zabytek sztuki romaskiej w Polsce.  Zwiedzanie Muzeum Pocztkw Pastwa Polskiego. Nastpnie przejazd do Biskupina.  Zwiedzanie Muzeum Archeologicznego w Biskupinie. Jest to jeden z najwikszych rezerwatw w Europie, obejmuje  okoo 38 ha z licznymi ladami dawnego osadnictwa, od epoki kamienia po wczesne redniowiecze. Po zwiedzaniu przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Przejazd do Poznania. Ostrw Tumski z Bazylik Archikatedraln witych Apostow Piotra i Pawa, bdc jedn z najstarszych wity w Polsce. Katedra jest miejscem pochwku krla Przemysa II, wedug tradycji rwnie Mieszka I i Bolesawa Chrobrego (cho to jest kwesti sporn). Nastpnie udamy si na zwiedzanie poznaskiego Starego Rynku, ktrego sercem jest renesansowy ratusz. W poudnie na wiey ratusza pojawiaj si synne kozioki. Oprcz ratusza zobaczymy rwnie domy patrycjuszy, prgierz, Bamberk  posta kobiety dwigajcej wiadra. Spacer do poznaskiej fary, ktra uchodzi za najpikniejszy w kraju barokowy koci. Zwiedzanie Muzeum Narodowego w Poznaniu- jest to jedna z najwaniejszych tego typu placwek w kraju. Zwiedzanie poczone z lekcj muzealn (do ustalenia).  Czas wolny.  Przejazd do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, wykwaterowanie. Przejazd do Mogilna, gdzie zobaczymy piknie pooone nad jeziorem XI-wieczne opactwo Benedyktynw. Nastpnie przejazd do Strzelna, gdzie znajduje si romaska rotunda w. Prokopa z XII wieku oraz romaska bazylika w. Trjcy z barokow fasad oraz odkrytymi w 1946 roku kolumnami z piaskowca, liczcymi okoo 800 lat. Przejazd do Kruszwicy, gdzie nad jeziorem Gopo zobaczymy owiane legend ruiny zamku.  Przejazd na obiad. Po obiedzie wyjazd w kierunku domu. Przyjazd w godzinach wieczornych/ pno-wieczornych. </p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Piastowie;