import React from 'react';
import '../styles/program.scss';

import FootstepsMaryGallery from './FootstepsMaryGallery';


const FootstepsMary = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Francja Hiszpania Portugalia</h1>
                    <h2>ladami sanktuariw Maryjnych</h2>
                    <h4>Maryja sama szuka takich, ktrzy by do Niej pobonie i z uszanowaniem przystpowali: tych bowiem miuje, tych karmi i zasila, tych za dzieci przyjmuje.
                       <span>w. Bonawentura</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <FootstepsMaryGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki. Przejazd w kierunku Niemiec i Francji. Przyjazd w okolice Strasburga. Obiadokolacja i nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Przejazd na zwiedzanie Strasburga, ktrego zabytkowe centrum zostao w caoci wpisane na List wiatowego Dziedzictwa UNESCO. Strasburg to rwnie miasto w ktrym przeszo spotyka si z nowoczesnoci, polityka z histori a alzacka mentalno z europejskim kosmopolityzmem. Spacer po Grand Ile, ktrej gwnym zabytkiem jest katedra Notre-Dame, nastpnie do urokliwej dzielnicy Petite France w ktrej znajduj si hotele i knajpki, w ktrych skosztowa mona typowo alzackiego flammekeche. Po zwiedzaniu przejazd w okolice La Salette. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Przejazd do La Salette- sanktuarium Matki Boskiej Paczcej zwizanego z objawieniem si Maryi dwjce pastuszkw. Czas na modlitw. Przejazd do Awinionu- miasta papiey, pooonego na brzegu Rodanu, bdcego per  Prowansji, gdzie na kadym kroku czu zapach lawendy i zi. Spacer po miasteczku, podczas ktrego zobaczymy: synny most awinioski  Most w. Benezeta, Paac Papieski, katedr Notre Dame de Domes, a cae redniowieczne miasteczko zamknite jest w wietnie zachowanych murach obronnych- les Remparts. Przejazd na kolacj i nocleg.</p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie, wykwaterowanie. Przejazd do Lourdes. Zakwaterowanie w hotelu. Obiadokolacja. Dla chtnych udzia w procesji maryjnej. Nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>Zwiedzanie miasteczka ladami w. Bernadety. Zobaczymy myn Boly, w ktrym si urodzia, Ciemnic i inne miejsca zwizane z jej yciem. Modlitwa w grocie, gdzie maa pastuszka dowiadczya spotkania z Matk Bosk. Droga krzyowa. Czas wolny na prywatn modlitw. Obiadokolacja. Udzia w procesji. Nocleg. </p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>Po niadaniu wyjazd w kierunku Saragossy. Nawiedzenie najwikszej barokowej bazyliki w Hiszpanii- Matki Boej na Pilarze. Tutaj Maryja objawia si w. Jakubowi. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Wyjazd na zwiedzanie Burgos, bdcego dawn stolic Starej Kastylii. Odwiedzimy tam jedn z najwspanialszych katedr gotyckich w caej Hiszpanii - katedry w. Jakuba. Spacer po miasteczku. Przejazd do Salamanki, o ktre nazywane jest miastem w kolorze zota. Zwiedzanie starej i nowej katedry. Nietypowe w niej jest to, e w rzeczywistoci s to dwie katedry poczone w jedn. Spacer na Plaza Mayor podczas ktrego bdziemy podziwia redniowieczn architektur. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

export default FootstepsMary;