import React from 'react';
import '../../styles/program.scss';
import BarcelonaGallery from './BarcelonaGallery';

const Barcelona = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Bajeczna Barcelona</h1>
                    {/* <h2>Na Szlaku Piastowskim</h2> */}
                    <h4>To miasto ma czarodziejsk moc (). Zanim si czowiek obejrzy, wejdzie mu pod skr i skradnie dusz.
                       <span>Carlos Ruiz Zafn</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <BarcelonaGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Odprawa na lotnisku, przelot do Barcelony, transfer do hotelu na wybrzeu Costa Brava, zakwaterowanie, kolacja, nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie, przejazd do Barcelony. W stolicy Katalonii zobaczymy najwaniejsze dziea Antoniego Gaudiego: Park Gell, bazylika Sagrada Famlia, fasady kamienic Casa Mila i Casa Batlo. Zobaczymy take Dzielnic Gotyck z Katedr w. Eulalii, spacer synn la Rambl, przejazd na wzgrze Montjuc oraz stadion FC Barcelony Camp Nou (patny ok. 25). Powrt na kolacj, nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, dzie wolny lub rejs statkiem do urokliwego miasteczka na wybrzeu Costa Brava - Tossa de Mar (patne ok. 25). Powrt do hotelu, kolacja, nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, wycieczka do Opactwa Matki Boej w Montserrat, ktre jest gwnym Sanktuarium Maryjnym w Katalonii. W klasztorze pooonym na malowniczym masywie grskim Montserrat znajduje si figurka Czarnej Madonny - La Morenety, ktra jest magnesem przycigajcym wszystkich wiernych. Powrt na kolacj. </p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, wykwaterowanie, transfer na lotnisko, wylot do Polski.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Barcelona;