import React from 'react';
import '../styles/program.scss';
import CroatiaGallery from './CroatiaGallery';

const Croatia = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Chorwacja</h1>
                    <h2>Chorwacja</h2>
                    <h4>Zadar ma najpikniejsze zachody soca na wiecie. Pikniejsze od tych oklaskiwanych kadego wieczora w Key West na Florydzie
                       <span>Wojciech Mynarski</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <CroatiaGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki o ustalonej godzinie. Przejazd w kierunku Czech. Zakwaterowanie w hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Wiednia. Objazd miasta reprezentacyjn alej miasta Ringstrasse, otaczajc historyczne centrum, przy ktrej ulokowane s najpikniejsze budowle w miecie m.in. Parlament, Ratusz, Opera Narodowa, Burgtheater itd.  Zobaczymy rwnie rezydencj cesarsk  Hofburg oraz Belveder. Nastpnie spacer po Starym Miecie, gdzie zobaczymy m.in. strzelist wiea katedry w. Szczepana, Kolhmarkt  targ wglowy, bdcy siedzib najbardziej ostentacyjnych, luksusowych domw mody. Czas wolny na krtki odpoczynek i kaw. Przejazd na nocleg w Sowieni. Zakwaterowanie w hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Wykwaterowanie. Spacer po miecie Maribor. Maribor i jego starwka przyciga ciekawymi zabytkami, kultur oraz gocinnoci mieszkacw. Na uwag zasuguj zwaszcza: pikne rynki miejskie z zabytkowymi, tradycyjnymi budynkami, dzielnica Lent, gdzie ronie najstarsza winorol na wiecie. Maribor synie z winiarskich tradycji, z doskonaych biaych win. Kadej jesieni organizowany jest tu Old Vine Festival. Opcjonalnie wizyta w jednej z okolicznych winnic. Czas wolny. Przejazd do Chorwacji. Obiadokolacja. Nocleg</p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV-VIII</h5>
                            <p>Pobyt w hotelu w okolicach Zadaru. Wybranego dnia zwiedzanie miasta z przewodnikiem. Zadar jest jednym z najpikniejszych miast Chorwacji i wg Alfreda Hitchcocka synie z najpikniejszych na wiecie zachodw soca. Zobaczymy m.in.: zabytkow Bram Ldow z cigiem kamiennych murw obronnych, Plac Piciu Studni, koci w. Donata najbardziej znany w Zadarze i jeden z najpikniejszych zabytkw Chorwacji, dawne rzymskie forum, koci NMP z wysok dzwonnic, katedr w. Anastazji, Brama Morska, spacer w kierunku morskich organw, na ktrych fale Adriatyku wygrywaj niezwyke melodie.</p>
                        </div>

                        <div className="ninth-day">
                            <h5>Dzie IX</h5>
                            <p>niadanie, wykwaterowanie. Wyjazd w kierunku Czech. Zakwaterowanie w hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="tenth-day">
                            <h5>Dzie X</h5>
                            <p>niadanie. Przejazd do Pragi.  Spacer po miecie podczas ktrego zobaczymy: klasztor na Strahovie, Hradczany z Zamkiem Krlewskim i katedr w. Wita, w. Wacawa i w. Wojciecha, synn Loret (kompleks barokowych budynkw sakralnych); Mala Strana - romantyczna dzielnica pooona na lewym brzegu Wetawy, u stp praskiego zamku. Nastpnie udamy si na Stare Miasto na prawym brzegu Wetawy, zobaczymy najbardziej reprezentacyjny plac miasta- Rynek Staromiejski na ktrym zlokalizowane s: ratusz, synny zegar Orloj, pomnik Jana Husa, Uniwersytet Karola z XIV wieku. Zobaczymy rwnie Plac Wacawa. W godzinach popoudniowych wyjazd w kierunku Polski.</p>
                        </div>
                        <div className="eleventh-day">
                            <h5>Dzie XI</h5>
                            <p>Przyjazd na miejsce zbirki w godzinach porannych dnia kolejnego.</p>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

export default Croatia;